<template>
    <div>
        <div style="display: flex;align-items: center; justify-content: flex-start;margin-bottom: 10px">

            <el-select v-model="rp.rpsystemId" clearable placeholder="" icon="el-icon-plus"
                       style="margin-right: 10px">
                <el-option v-for="item in rpsystems" :key="item.id" :label="item.rpsystem" :value="item.id"></el-option>
            </el-select>
            <el-tooltip content="" placement="top" effect="light">
                <el-input-number v-model="rp.rpiont" placeholder="" controls-position="right" :min="-10" :max="10"
                                 style="margin-right: 10px"/>
            </el-tooltip>
            <el-autocomplete class="inline-input" v-model="rp.rpmsg" :fetch-suggestions="querySearch" placeholder=""
                             style="margin-right: 10px">
                <i class="el-icon-edit el-input__icon" slot="suffix"></i>
            </el-autocomplete>
            <el-button  type="primary"    icon="el-icon-plus" @click="addrwardsPunishments"></el-button>
            <el-input
                    class="addPosInput"
                    placeholder="..."
                    prefix-icon="el-icon-search"
                    :fetch-suggestions="querySearch"
                    v-model="rp.searchname">
            </el-input>
            <el-button     icon="el-icon-search"  type="primary" @click="searchname"></el-button>

        </div>
        <div style="margin-top: 10px">
            <el-table
                    :data="rps"
                    border
                    v-loading="loading"
                    element-loading-spinner="el-icon-loading"
                    @selection-change="handleSelectionChange"
                    size="small"
                    stripe
                    style="width:80%">
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column prop="id" label="" width="55">
                </el-table-column>
                <el-table-column prop="rwardsAndPunishments.rpsystem" label="" width="150">
                </el-table-column>
                <el-table-column prop="rpiont" label="" width="100">
                </el-table-column>
                <el-table-column prop="rpmsg" label="" width="250">
                </el-table-column>
                <el-table-column prop="createrp" label="" width="250">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button     size="mini" @click="showEditView(scope.row)"></el-button>
                        <el-button     size="mini" type="danger" @click="deleteHandler(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-dialog
                    title=""
                    :visible.sync="dialogVisible"
                    width="30%">
                <div>
                    <el-form :rules="rules">
                        <el-form-item label="" prop="updaterp.name">
                            <el-select v-model="updaterp.rpsystemId" clearable placeholder="" icon="el-icon-plus"
                                       style="margin-right: 10px">
                                <el-option v-for="item in rpsystems" :key="item.id" :label="item.rpsystem" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="rp.rpiont">
                            <el-input-number v-model="updaterp.rpiont" placeholder="" controls-position="right" :min="-10" :max="10"
                                             style="margin-right: 10px"/>
                        </el-form-item>
                        <el-form-item label="" prop="rp.rpmsg">
                            <el-autocomplete class="inline-input" v-model="updaterp.rpmsg" :fetch-suggestions="querySearch" placeholder=""
                                             style="margin-right: 10px">
                                <i class="el-icon-edit el-input__icon" slot="suffix"></i>
                            </el-autocomplete>
                        </el-form-item>
                    </el-form>
                </div>
                <span slot="footer" class="dialog-footer">
    <el-button    size="small" @click="outUpdate"> </el-button>
    <el-button    size="small" type="primary" @click="doUpdate"> </el-button>
  </span>
            </el-dialog>
            <el-button    type="danger" style="margin-top: 10px" :disabled="multipleSelection.length==0"
                       @click="deleteMany">
            </el-button>
            <el-button    @click="refershMany" type="primary" style="margin-top: 8px"> 
            </el-button>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                dialogVisible:false,
                rpsystems: [],
                rpsystem: '',
                rps: [],
                rp: {
                    rpsystemId: '',
                    rpmsg: '',
                    rpiont: '1',
                    searchname:''
                },
                updaterp: {
                    rpsystemId: '',
                    rpmsg: '',
                    rpiont: '',
                    searchname:'',
                    rwardsAndPunishments: {
                    rpsystem: ""},
                },
                restaurants: [],
                loading: false,
                multipleSelection: [],
            }
        },
        mounted() {
            this.initrps();
            this.initrpsystems();
            this.restaurants = this.initrpmsg();
        },
        methods: {
            /**/
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            deleteMany(){
            this.$confirm('' + this.multipleSelection.length + ', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let ids = '?';
                this.multipleSelection.forEach(item => {
                    ids += 'ids=' + item.id + '&';
                })
                this.$notify.success({
                    title: '',
                    message: '    ...',
                    showClose: false,
                    offset: 100,
                    duration: 4000,
                    customClass: 'fontclass'
                });
                this.deleteRequest("/system/basic/rp/" + ids).then(resp => {
                    if (resp) {
                        this.initrps();
                    }
                })
            }).catch(() => {
                this.$notify.info({
                    title: '',
                    message: '     ',
                    showClose: false,
                    offset: 100,
                    duration: 4000,
                    customClass: 'fontclass'
                });
            });
            },
            showEditView(data) {
                //
                /*sourcetarget*/
                Object.assign(this.updaterp, data);
                this.dialogVisible = true;
            },
            outUpdate() {
                this.$notify.info({
                    title: '',
                    message: '    ...',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });
                this.dialogVisible = false;
            },
            doUpdate() {
                this.$notify.success({
                    title: '',
                    message: '    ...',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });

                this.putRequest("/system/basic/rp/", this.updaterp).then(resp => {
                    if (resp) {
                        this.initrps();
                        this.dialogVisible = false;
                    }
                })
            },
            searchname(){
                if ( this.rp.searchname) {
                    this.rp.rpmsg = this.rp.searchname;
                    this.postRequest("/system/basic/rp/serch/", this.rp).then(resp => {
                        if (resp) {
                            this.rps = resp;
                            this.rp.rpmsg = '';
                            this.rp.searchname = '';
                        }
                    });
                }else {
                    this.$notify({
                        title: '',
                        message: '        !...',
                        showClose: false,
                        offset: 100,
                        duration: 2000,
                        customClass: 'fontclass',
                        type: 'warning'
                    });
                }
            },
            deleteHandler(data) {
                this.$confirm('' + data.rpmsg + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$notify.success({
                        title: '',
                        message: '    ...',
                        showClose: false,
                        offset: 100,
                        duration: 3000,
                        customClass: 'fontclass'
                    });
                    this.deleteRequest("/system/basic/rp/" + data.id).then(resp => {
                        if (resp) {
                            this.initrps();
                        }
                    })
                }).catch(() => {
                    this.$notify.info({
                        title: '',
                        message: '     ...',
                        showClose: false,
                        offset: 100,
                        duration: 3000,
                        customClass: 'fontclass'
                    });
                });
            },
            refershMany(){
                this.initrps();
            },
            querySearch(queryString, cb) {
                var restaurants = this.restaurants;
                var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
                //  callback 
                cb(results);
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) > -1);
                };
            },
            initrpmsg() {
                return [
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                    {"value": ""},
                ];
            },
            addrwardsPunishments() {
                if (this.rp.rpsystemId && this.rp.rpmsg && this.rp.rpiont) {
                    this.$notify.success({
                        title: '',
                        message: '      ...',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass'
                    });
                    this.postRequest("/system/basic/rp/", this.rp).then(resp => {
                        if (resp) {
                            this.rp.rpsystemId = '',
                            this.rp.rpmsg = '',
                            this.rp.rpiont = '1'
                            this.initrps();
                        }
                    });
                }else {
                    this.$notify({
                        title: '',
                        message: '        !',
                        showClose: false,
                        offset: 100,
                        duration: 2000,
                        customClass: 'fontclass',
                        type: 'warning'
                    });
                }
            },
            initrpsystems(){
              this.getRequest("/system/basic/rp/rap/").then( resp =>{
                  if (resp){
                      this.rpsystems = resp;
                  }
              });
            },
            initrps(){
                setTimeout(() => {
                    this.$notify.success({
                        title: '',
                        message: '        ...',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass'
                    });
                }, 1200)
               this.getRequest("/system/basic/rp/").then( resp =>{
                   if (resp){
                       this.rps = resp;
                   }
               }) ;
            }
        }
    }
</script>

<style >
    .fontclass {
        font-size: 35px;
        font-family: ;
    }
</style>