<template>
    <div>
        <div style="display: flex;justify-content: flex-start">
            <el-input v-model="jl.name" style="width: 300px;" prefix-icon="el-icon-plus"
                      placeholder="..."></el-input>
            <el-select v-model="jl.titlelevel" placeholder=""
                       style="margin-left: 5px;margin-right: 5px">
                <el-option
                        v-for="item in titlelevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button    icon="el-icon-plus" type="primary" @click="addJobLevel"></el-button>
            <el-input

                    class="addPosInput"
                    placeholder="..."
                    prefix-icon="el-icon-search"
                    v-model="jl.searchname">
            </el-input>
            <el-button    icon="el-icon-search" type="primary" @click="searchname"></el-button>

        </div>
        <div style="margin-top: 10px">
            <el-table
                    :data="jls"
                    border
                    v-loading="loading"
                    element-loading-spinner="el-icon-loading"
                    size="small"
                    stripe
                    style="width:80%"
                    @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column prop="id" label="" width="55">
                </el-table-column>
                <el-table-column prop="name" label="" width="150">
                </el-table-column>
                <el-table-column prop="titlelevel" label="" width="150">
                </el-table-column>
                <el-table-column prop="createdate" label="" width="200">
                </el-table-column>
                <el-table-column label="" width="150">
                    <template slot-scope="scope">
                        <el-tag type="success" size="small" v-if="scope.row.enabled"></el-tag>
                        <el-tag type="danger" size="small" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button    size="mini" @click="showEditView(scope.row)"></el-button>
                        <el-button    size="mini" type="danger" @click="deleteHandler(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: flex-start">
            <el-button    type="danger" style="margin-top: 10px" :disabled="multipleSelection.length==0"
                       @click="deleteMany">
            </el-button>
            <el-button    @click="refershMany" type="primary" style="margin-top: 8px"> 
            </el-button>
            </div>
        </div>
        <el-dialog title="" :visible.sync="dialogVisible" width="30%">
            <div>
                <el-form :rules="rules">
                    <el-form-item label="" prop="updateJl.name">
                        <el-input type="text" class="updatePosInput" size="small" v-model="updateJl.name"/>
                    </el-form-item>
                    <el-form-item label="" prop="updateJl.titlelevel">
                        <el-select v-model="updateJl.titlelevel" placeholder="" size="small"
                                   style="margin-left: 5px;margin-right: 5px">
                            <el-option v-for="item in titlelevels" :key="item" :label="item" :value="item">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="updateJl.enabled">
                        <el-switch v-model="updateJl.enabled" style="margin-left: 15px">
                        </el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button    size="small" @click="outUpdate"> </el-button>
    <el-button    size="small" type="primary" @click="doUpdate"> </el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "JobLevelMana",
        data() {
            return {
                dialogVisible: false,
                loading: false,
                multipleSelection: [],
                updateJl: {
                    name: '',
                    titlelevel: '',
                    enabled: false
                },
                jl: {
                    name: '',
                    titlelevel: '',
                    searchname: '',
                },
                jls: [],
                titlelevels: [
                    '',
                    '',
                    '',
                    '',
                    '',
                ]
            }
        },
        mounted() {
            this.initJls();
        },
        methods: {
            refershMany() {
                this.initJls();
            },
            searchname() {
                if (this.jl.searchname) {
                    this.$notify.success({
                        title: '',
                        message: '    ...',
                        showClose: false,
                        offset: 100,
                        duration: 2000,
                        customClass: 'fontclass'
                    });
                    this.jl.name = this.jl.searchname;
                    this.postRequest("/system/basic/joblevel/serch/", this.jl).then(resp => {
                        if (resp) {
                            /*+*/
                            this.jls = resp;
                            this.jl.searchname = '';
                            this.jl.name = '';
                        }
                    })
                } else {
                    this.$notify({
                        title: '',
                        message: '        !...',
                        showClose: false,
                        offset: 100,
                        duration: 2000,
                        customClass: 'fontclass',
                        type: 'warning'
                    });
                }
            },
            deleteMany() {
                this.$confirm('' + this.multipleSelection.length + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.multipleSelection.forEach(item => {
                        ids += 'ids=' + item.id + '&';
                    })
                    this.$notify.success({
                        title: '',
                        message: '    ...',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass'
                    });
                    this.deleteRequest("/system/basic/joblevel/" + ids).then(resp => {
                        if (resp) {
                            this.initJls();
                        }
                    })
                }).catch(() => {
                    this.$notify.info({
                        title: '',
                        message: '     ',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass'
                    });
                });
            },
            outUpdate() {
                this.dialogVisible = false;
                this.$notify.info({
                    title: '',
                    message: '       ',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });
            },
            doUpdate() {
                this.$notify.success({
                    title: '',
                    message: '     ...',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });
                this.putRequest("/system/basic/joblevel/", this.updateJl).then(resp => {
                    if (resp) {
                        this.initJls();
                        this.dialogVisible = false;
                    }
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            showEditView(data) {
                Object.assign(this.updateJl, data);
                this.dialogVisible = true;
            },
            deleteHandler(data) {
                this.$confirm('' + data.name + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$notify.success({
                        title: '',
                        message: '    ...',
                        showClose: false,
                        offset: 100,
                        duration: 3000,
                        customClass: 'fontclass'
                    });
                    this.deleteRequest("/system/basic/joblevel/" + data.id).then(resp => {
                        if (resp) {
                            this.initJls();
                        }
                    })
                }).catch(() => {
                    this.$notify.info({
                        title: '',
                        message: '     ...',
                        showClose: false,
                        offset: 100,
                        duration: 3000,
                        customClass: 'fontclass'
                    });
                });
            },
            addJobLevel() {
                if (this.jl.name && this.jl.titlelevel) {
                    this.$notify.success({
                        title: '',
                        message: '    ...',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass'
                    });
                    this.postRequest("/system/basic/joblevel/", this.jl).then(resp => {
                        if (resp) {
                            this.initJls();
                        }
                    });
                } else {
                    this.$notify({
                        title: '',
                        message: '        ...',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass',
                        type: 'warning'
                    });
                }
            },
            /**/
            initJls() {
                setTimeout(() => {
                    this.$notify.success({
                        title: '',
                        message: '      ...',
                        showClose: false,
                        offset: 100,
                        duration: 4000,
                        customClass: 'fontclass'
                    });
                }, 1000);

                this.loading = true;
                this.getRequest("/system/basic/joblevel/").then(resp => {
                    this.loading = false;
                    if (resp) {
                        this.jls = resp;
                        this.jl = {
                            name: '',
                            titleLevel: ''
                        };
                    }
                })
            }
        }
    }
</script>

<style scoped >

    .fontclass {
        font-size: 35px;
        font-family: ;
    }

</style>