<template>
  <div class="edit_container">
    <div class="topdstyle">
      <el-input placeholder=""
                v-model="mainnotice.title"
                style="width: 790px">
        <template slot="prepend"
                  style="font-family: "></template>
      </el-input>
      <el-button v-loading.fullscreen.lock="loading"
                 element-loading-spinner="fa fa-spinner fa-pulse fa-3x fa-fw"
                 type="primary"
                 class="depBtn"
                 icon="el-icon-plus"
                 style="width: 194px ;margin-left: 10px"
                 @click="saveHtml"> 
      </el-button>
    </div>
    <div class="centontstyle">
      <el-scrollbar style="height:100%;width: 1500px">
        <quill-editor v-model="mainnotice.mainbody"
                      ref="myQuillEditor"
                      :options="editorOption"
                      @blur="onEditorBlur($event)"
                      @ready="onEditorReady($event)"
                      @focus="onEditorFocus($event)"
                      @change="onEditorChange($event)">
        </quill-editor>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>


export default {
  inject: ["reload"],
  data () {
    return {
      mainnotice: {
        title: '',
        mainbody: '',
      },
      loading: false,
      content: "",
      editorOption: {
        modules: {
          toolbar: [

            ["link", "image", "video"],// 
            ["bold", "italic", "underline", "strike"], //    
            ["blockquote", "code-block"], //   
            [{ header: 1 }, { header: 2 }], // 12 
            [{ list: "ordered" }, { list: "bullet" }], // 
            [{ script: "sub" }, { script: "super" }], // /
            [{ indent: "-1" }, { indent: "+1" }], // 
            [{ size: ["small", false, "large", "huge"] }], // 
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
            [{ color: [] }, { background: [] }], // 
            [{ align: [] }], // 
            ["clean"], // 
          ], //
        },
        placeholder: '', //
        readyOnly: false, //
        theme: 'snow', // snow/bubble
        syntax: true, //
      }
    }
  }, computed: {
    editor () {
      return this.$refs.myQuillEditor.quill;
    },
  }, methods: {
    onEditorReady () { // 
    },
    onEditorBlur () {
    }, // 
    onEditorFocus () {
    }, // 
    onEditorChange () {
    }, // 
    saveHtml: function () {
      if (this.mainnotice.mainbody && this.mainnotice.title) {
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
        this.loading = true;

        this.postRequest('/system/cfg/', this.mainnotice).then(resp => {
          this.loading = false;
          if (resp) {
            this.loading = false;
            this.mainnotice = [];
            this.reload();
            this.mainnotice = [];
            this.content = '';
          }
        });
      } else {
        this.$notify({
          title: '',
          message: '       ...',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass',
          type: 'warning'
        });
      }
    }
  }
}
</script>

<style>
.topdstyle {
  display: flex;
  justify-content: flex-start;
}

.centontstyle {
  display: flex;
  margin-top: 10px;
  height: 690px;
  overflow-x: hidden;
  width: 1000px;
}

.centontstyle .el-scrollbar__wrap {
  overflow-x: hidden;

  background-clip: padding-box;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.editor {
  line-height: normal !important;
}

.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: ":";
}

.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}

.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}

.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}

.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}

</style>