<template>
  <div>
    <div class="top-style">
      <div style="display: flex;justify-content: flex-start;">
        <el-input placeholder="..."
                  prefix-icon="el-icon-search"
                  clearable
                  @clear="initmain"
                  style="width: 350px;margin-right: 10px"
                  v-model="keyword"
                  @keydown.enter.native="initmain"></el-input>
        <el-button icon="el-icon-search"
                   type="primary"
                   @click="initmain">
          
        </el-button>
      </div>
    </div>
    <div class="content-styles">
      <el-scrollbar style="height: 100%;">
        <div>
          <el-table :data="mainnotices"
                    border
                    v-loading.fullscreen.lock="loading"
                    element-loading-spinner="fa fa-spinner fa-pulse fa-3x fa-fw"
                  
                    stripe
                    height="660"
                    style="width:80%;height: 645px"
                    @selection-change="handleSelectionChange">
            <el-table-column type="selection"
                             width="55">
            </el-table-column>
            <el-table-column prop="id"
                             label=""
                             width="55">
            </el-table-column>
            <el-table-column prop="title"
                             label=""
                             width="150">
            </el-table-column>
            <el-table-column prop="authon"
                             label=""
                             width="150">
            </el-table-column>
            <el-table-column prop="updateTime"
                             label=""
                             width="200">
            </el-table-column>
            <el-table-column prop="createTime"
                             label=""
                             width="200">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button size="mini"
                           type="success"
                           @click="showView(scope.row)"></el-button>
                <el-button size="mini"
                           type="danger"
                           @click="deleteHandler(scope.row)"></el-button>
                <el-button size="mini"
                           type="primary"
                           @click="showEditView(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-scrollbar>
    </div>
    <div class="bottom-style">
      <div>
        <el-button type="danger"
                   style="margin-top: 10px"
                   :disabled="multipleSelection.length==0"
                   @click="deleteMany">
        </el-button>
        <el-button @click="refershMany"
                   type="primary"
                   style="margin-top: 8px"> 
        </el-button>
      </div>
      <el-pagination style="margin-top: 10px;margin-right:320px"
                     background
                     @current-change="currentChange"
                     @size-change="sizeChange"
                     layout="sizes, prev, pager, next, jumper, ->, total, slot"
                     :total="total"
                     :page-sizes="[11,22,333,4444]">
      </el-pagination>
    </div>
    <el-drawer :before-close="handleClose"
               :visible.sync="dialog"
               direction="ltr"
               custom-class="drawerstyleleft"
               ref="drawer"
               size="40%">
      <div class="center-right-infinite-list">
        <el-scrollbar style="height: 100%;width:100%">
          <!-- <div v-html="mainnotice.mainbody">  -->
          <quill-editor v-model="mainnotice.mainbody"
                        disabled="value"
                        :options="editorOption">
          </quill-editor>
        </el-scrollbar>
      </div>
    </el-drawer>
    <el-drawer :before-close="handleClose"
               :visible.sync="updatedialog"
               direction="rtl"
               custom-class="drawerstyleright"
               ref="drawer"
               size="40%"
               @close="updatemainnotice">
      <div class="center-right-infinite-list">
        <el-scrollbar style="height: 100%;width:100%">
          <!-- <div v-html="mainnotice.mainbody">  -->
          <quill-editor v-model="mainnotice.mainbody"
                        :options="editorOption">
          </quill-editor>
        </el-scrollbar>
      </div>
    </el-drawer>
  </div>
</template>


<script>

export default {

  name: "Amanagement",
  data () {
    return {
      editorOption: {
        modules: {
          toolbar: [

            ["link", "image", "video"],// 
            ["bold", "italic", "underline", "strike"], //    
            ["blockquote", "code-block"], //   
            [{ header: 1 }, { header: 2 }], // 12 
            [{ list: "ordered" }, { list: "bullet" }], // 
            [{ script: "sub" }, { script: "super" }], // /
            [{ indent: "-1" }, { indent: "+1" }], // 
            [{ size: ["small", false, "large", "huge"] }], // 
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
            [{ color: [] }, { background: [] }], // 
            [{ align: [] }], // 
            ["clean"], // 
          ], //
        },
        placeholder: '', //
        readyOnly: false, //
        theme: 'bubble', // snow/bubble
        syntax: true, //
      },
      mainnotice: {
        id: '',
        title: '',
        mainbody: '',

      },
      total: 0,
      page: 1,
      keyword: '',
      size: 11,
      loading: false,
      dialog: false,
      updatedialog: false,
      mainnotices: [],
      multipleSelection: [],
    }
  },
  mounted () { this.initmain(); },
  methods: {

    showEditView (data) {
      this.updatedialog = true;
      this.mainnotice = data;
    },
    showView (data) {
      this.dialog = true;
      this.mainnotice = data;
    },
    /**/
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    updatemainnotice () {
      this.$notify.success({
        title: '',
        message: '     ...',
        showClose: false,
        offset: 100,
        duration: 2000,
        customClass: 'fontclass'
      });
      this.putRequest("/system/cfg/", this.mainnotice).then(resp => {
        if (resp) {
          this.initmain();

        }
      })
    },
    deleteMany () {
      this.$confirm('' + this.multipleSelection.length + ', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let ids = '?';
        this.multipleSelection.forEach(item => {
          ids += 'ids=' + item.id + '&';
        })
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
        this.deleteRequest("/system/cfg/" + ids).then(resp => {
          if (resp) {
            this.initmain();
          }
        })
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: '     ',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
      });
    },
    refershMany () {
      this.initmain();
    },
    deleteHandler (data) {
      this.$confirm('' + data.title + ', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$notify.success({
          title: '',
          message: '    ...',
          showClose: false,
          offset: 100,
          duration: 4000,
          customClass: 'fontclass'
        });
        this.deleteRequest("/system/cfg/" + data.id).then(resp => {
          if (resp) {
            this.keyword = '';
            this.initmain();
          }

        });
      }).catch(() => {
        this.$notify.info({
          title: '',
          message: '   ',
          showClose: false,
          offset: 100,
          duration: 2000,
          customClass: 'fontclass'
        });
      });

    },
    initmain () {
      this.loading = true;
      this.getRequest("/system/cfg/?page=" + this.page + "&size=" + this.size + "&keyword=" + this.keyword).then(resp => {
        if (resp) {
          this.loading = false;
          this.mainnotices = resp.data;
          this.total = resp.total;
          this.keyword = '';
        }
      })

    },
    /**/
    sizeChange (currentSize) {
      this.size = currentSize;
      this.initmain();
    },
    currentChange (currentPage) {
      this.page = currentPage;
      this.initmain();
    },
  },

}
</script>

<style>
.center-right-infinite-list {
  width: 750px;
  height: 740px;
}
.center-right-infinite-list .el-scrollbar__wrap {
  overflow-x: hidden;
}

.fontclass {
  font-family: ;
}

.el-drawer.drawerstyleleft {
  border-radius: 0px 15px 15px 0px;
  box-shadow: dodgerblue;
}
.el-drawer .drawerstyleright {
  border-radius: 15px 0px 0px 15px;
  box-shadow: dodgerblue;
}
.top-style {
  /**/
  display: flex;
  /**/
  justify-content:flex-start;
  
}

.content-styles {
  margin-top: 10px;
}

.content-styles .el-scrollbar__wrap {
  overflow-x: hidden;
}

.bottom-style {
  display: flex;
  justify-content: space-between;
}

.fontclasssys {
  font-family: ;
}
.editor {
  line-height: normal !important;
}
.ql-bubble .ql-tooltip[data-mode="link"]::before {
  content: ":";
}

.ql-bubble .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-bubble .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-bubble .ql-picker.ql-size .ql-picker-label::before,
.ql-bubble .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}

.ql-bubble .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-bubble .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}

.ql-bubble .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-bubble .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}

.ql-bubble .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-bubble .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}

.ql-bubble .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-bubble .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}

.ql-bubble .ql-picker.ql-font .ql-picker-label::before,
.ql-bubble .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}

.ql-bubble .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-bubble .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}

.ql-bubble .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-bubble .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}
</style>